<template>
  <v-card
    :color="color"
    class="white--text mb-3"
    height="150"
    style="cursor:pointer"
    :hover="hover"
    raised
  >
    <v-container>
      <v-layout>
        <v-flex xs8 offset-xs2 sm6 offset-sm3 class="mt-4" justify-center>
          <p class="headline">{{text}}</p>
        </v-flex>
      </v-layout>
    </v-container>
  </v-card>
</template>
<script>
export default {
  props: {
    text: {
      type: String,
      required: true
    },
    color: {
      type: String,
      default: "blue-grey darken-2"
    }
  },
  data() {
    return {
      hover: true
    };
  },
  methods: {
    emitEvent() {
      this.$emit("itemClick");
    }
  }
};
</script>

<style scoped>
p.headline {
  text-align: center;
}
</style>
