<template>
  <v-dialog v-model="converterDialog" persistent full-width>
    <item-view slot="activator" :text="baseConverter.text" :color="baseConverter.color"></item-view>
    <v-layout justify-center row wrap>
      <v-flex md6>
        <v-card>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="ml pa-2 ml-2"
              color="red darken-1"
              right
              flat
              @click="converterDialog = false"
            >
              <v-icon right class="font-weight-bold">close</v-icon>
            </v-btn>
          </v-card-actions>
          <div primary-title class="headline primary--text">
            <p class="text-xs-center">Base Converter</p>
          </div>
          <v-divider></v-divider>
          <v-card-text>
            <v-layout justify-center column>
              <v-flex xs12 v-if="show" class="mb-5">
                <Alert @alertClose="alertClose" :message="message"/>
              </v-flex>

              <v-flex xs12 v-else-if="error" class="mb-5">
                <Alert @alertClose="alertClose" :type="'error'" :message="errorMessage"/>
              </v-flex>

              <v-flex xs12 class="mb-3">
                <v-layout row wrap>
                  <v-flex xs3>
                    <div class="mt-2 mr-3 pa-2 headline text-md-right">From:</div>
                  </v-flex>
                  <v-flex xs9>
                    <v-select clearable :items="items" v-model="baseFrom" label="Select Base"></v-select>
                  </v-flex>
                </v-layout>
              </v-flex>
              <v-flex xs12 class="mb-3">
                <v-layout row wrap>
                  <v-flex xs3>
                    <div class="mt-2 mr-5 pa-2 headline text-md-right">To:</div>
                  </v-flex>
                  <v-flex xs9>
                    <v-select clearable :items="items" v-model="baseTo" label="Select Base"></v-select>
                  </v-flex>
                </v-layout>
              </v-flex>
              <v-flex xs12 class="mb-3">
                <v-layout row wrap>
                  <v-flex xs3></v-flex>
                  <v-flex xs9>
                    <v-text-field
                      v-model="enteredValue"
                      label="Enter value to convert here"
                      clearable
                    ></v-text-field>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="convert" flat class="primary text-capitalize font-weight-bold">Convert</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-dialog>
</template>
<script>
import ItemView from "@/components/ItemView";
import Alert from "@/components/Alert";
import { Converter } from "@/util/converter";
export default {
  components: {
    ItemView,
    Alert
  },
  data() {
    return {
      converterDialog: false,
      showValue: false,
      message: "",
      type: "",
      errorStatus: false,
      excessValue: false,
      errorMessage: "",
      baseFrom: "",
      baseTo: "",
      enteredValue: "",
      baseConverter: {
        color: "teal",
        text: "Base Converter"
      }
    };
  },
  computed: {
    show() {
      return this.showValue;
    },
    error() {
      return this.errorStatus;
    },
    items() {
      let items = [];
      for (let i = 2; i < 37; i++) {
        items.push(i);
      }
      return items;
    }
  },
  created() {
    this.converter = new Converter();
  },
  methods: {
    alertClose() {
      this.showValue = false;
      this.errorStatus = false;
    },
    convert() {
      if (!this.baseFrom || !this.baseTo) {
        let result = `Error: Please select the bases you want to convert from and convert to`;
        return this.handleResult(result);
      }

      if (!this.enteredValue) {
        let result =
          "Error: Please enter a value you want to convert to another base";
        return this.handleResult(result);
      }

      if (+this.baseFrom === 2) {
        let result = this.convertFromBaseTwo(
          this.enteredValue,
          this.baseFrom,
          this.baseTo
        );
        this.handleResult(result);
      } else if (+this.baseFrom > 2 && +this.baseFrom < 10) {
        let result = this.convertFromBaseLessThanTen(
          this.enteredValue,
          this.baseFrom,
          this.baseTo
        );
        this.handleResult(result);
      } else if (+this.baseFrom === 10) {
        let result = this.convertFromBaseTen(this.enteredValue, this.baseTo);
        this.handleResult(result);
      } else {
        let result = this.convertFromBaseGreaterThanTen(
          this.enteredValue,
          this.baseFrom,
          this.baseTo
        );
        this.handleResult(result);
      }
    },
    handleResult(result) {
      if (new String(result).substr(0, 5) === "Error") {
        this.errorStatus = true;
        this.showValue = false;
        return (this.errorMessage = result);
      } else {
        this.message = `${result} base ${this.baseTo}`;
        this.showValue = true;
      }
    },
    convertFromBaseTwo(valueToConvert, baseFrom, baseTo) {
      if (this.verifyBinary(valueToConvert)) {
        if (+baseTo === 10) {
          return this.converter.convertToDecimal(valueToConvert, baseFrom);
        } else {
          let baseTenValue = this.converter.convertToDecimal(
            valueToConvert,
            baseFrom
          );
          return this.converter.convertToOtherBase(baseTenValue, baseTo);
        }
      } else {
        return "Error: Please enter correct binary number";
      }
    },
    convertFromBaseLessThanTen(valueToConvert, baseFrom, baseTo) {
      if (isNaN(+valueToConvert)) {
        return `Error: Invalid input, check the value you entered: ${valueToConvert} and retry`;
      }
      if (this.withinRange(valueToConvert, baseFrom)) {
        if (+baseTo === 10) {
          return this.converter.convertToDecimal(valueToConvert, baseFrom);
        } else {
          let baseTenValue = this.converter.convertToDecimal(
            valueToConvert,
            baseFrom
          );
          return this.converter.convertToOtherBase(baseTenValue, baseTo);
        }
      } else {
        return `Error: Enter correct number within base ${baseFrom} range`;
      }
    },
    convertFromBaseGreaterThanTen(valueToConvert, baseFrom, baseTo) {
      let value = valueToConvert.toUpperCase();

      if (+baseTo === 10) {
        return this.converter.convertToDecimal(value, baseFrom);
      } else {
        let baseTenValue = this.converter.convertToDecimal(value, baseFrom);

        return this.converter.convertToOtherBase(baseTenValue, baseTo);
      }
    },
    convertFromBaseTen(valueToConvert, baseTo) {
      return this.converter.convertToOtherBase(valueToConvert, baseTo);
    },
    verifyBinary(value) {
      if (isNaN(+value)) {
        return false;
      }
      let valueArray = value.split("");
      for (let val of valueArray) {
        if (val > 1) {
          return false;
        }
      }
      return true;
    },

    withinRange(numberToConvert, base) {
      for (let i = 0; i < numberToConvert.length; i++) {
        if (+numberToConvert[i] > base || +numberToConvert[i] === base) {
          return false;
        }
      }
      return true;
    }
  }
};
</script>
