<template>
  <div>
    <v-alert
      :value="show"
      :type="type"
      @input="onClose"
      transition="scale-transition"
      :dismissible="dismissible"
      class="headline"
    >{{message}}</v-alert>
  </div>
</template>
<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: true
    },
    dismissible: {
      type: Boolean,
      default: true
    },
    type: {
      type: String,
      default: "success"
    },
    message: {
      type: String,
      required: true
    }
  },
  methods: {
    onClose() {
      this.$emit("alertClose");
    }
  }
};
</script>
