export const Stack = (function() {
  let _mapItems = new WeakMap();
  class Stack {
    constructor() {
      _mapItems.set(this, []);
    }
    push(val) {
      _mapItems.get(this).push(val);
    }
    pop() {
      return _mapItems.get(this).pop();
    }
    peek() {
      let stack = _mapItems.get(this);
      return stack[stack.length - 1];
    }
    size() {
      return _mapItems.get(this).length;
    }
    isEmpty() {
      return this.size === 0;
    }
    clear() {
      _mapItems.set(this, []);
    }
    print() {
      return _mapItems.get(this);
    }
    _mapItems() {
      return _mapItems;
    }
  }
  return Stack;
})();
