export function HashTable() {
  let table = [];
  this.put = (key, value) => {
    let position = djb2HashCode(key);
    table[position] = value;
  };
  this.get = key => {
    let postion = djb2HashCode(key);
    return table[postion];
  };
  this.delete = key => {
    table[djb2HashCode(key)] = undefined;
  };
  this.print = () => {
    console.log(table);
  };

  function djb2HashCode(key) {
    let hash = 5381;

    if (!isNaN(parseInt(key))) {
      hash = key + "";
    }

    let i;
    for (i = 0; i < key.length; i++) {
      hash = hash * 33 + key.charCodeAt(i);
    }
    return hash % 1013;
  }
}
