<template>
  <v-dialog v-model="figureDialog" light persistent full-width>
    <item-view slot="activator" :text="figureToWords.text" :color="figureToWords.color"></item-view>
    <v-layout justify-center>
      <v-flex md6>
        <v-card>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="ml pa-2 ml-2"
              color="red darken-1"
              right
              flat
              @click="figureDialog = false"
            >
              <v-icon right class="font-weight-bold">close</v-icon>
            </v-btn>
          </v-card-actions>

          <div primary-title class="headline primary--text">
            <p class="text-xs-center">Figure To Words Translator</p>
          </div>
          <v-divider></v-divider>
          <v-card-text class="mt-4">
            <v-layout justify-center column>
              <v-flex xs12 v-if="show" class="mb-5">
                <Alert @alertClose="alertClose" :message="message"/>
              </v-flex>

              <v-flex xs12 v-else-if="error" class="mb-5">
                <Alert @alertClose="alertClose" :type="'error'" :message="errorMessage"/>
              </v-flex>

              <v-flex xs12>
                <v-text-field
                  v-model="valueEntered"
                  label="Enter Figure/Number Here"
                  clearable
                  :rules="rules"
                  counter="21"
                  class="text-xs-center primary--text"
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              flat
              class="primary text-capitalize font-weight-bold"
              @click="translate"
            >Translate</v-btn>
          </v-card-actions>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-switch color="primary font-weigth-bold" v-model="voiceEnabled" :label="voiceNote"></v-switch>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-dialog>
</template>
<script>
import ItemView from "@/components/ItemView";
import Alert from "@/components/Alert";
import { FigureConverter } from "@/util/number_to_figure.js";
export default {
  components: {
    ItemView,
    Alert
  },
  data() {
    return {
      figureDialog: false,
      figureToWords: {
        color: "teal",
        text: "Figure to Words Translator"
      },
      voiceEnabled: false,
      voiceNote: "Enable Voice Note",
      showValue: false,
      message: "",
      type: "",
      errorStatus: false,
      excessValue: false,
      errorMessage: "",
      valueEntered: "",
      disableVoice: false,
      rules: [
        v => {
          if (v) {
            if (v.length > 21) {
              this.excessValue = true;
            } else {
              this.excessValue = false;
            }
            return v.length <= 21 || "Maximum of 21 digits";
          } else {
            this.excessValue = false;
            return false;
          }
        }
      ]
    };
  },
  watch: {
    valueEntered(newValue, oldValue) {
      if (!newValue) {
        this.errorStatus = false;
        this.showValue = false;
      }
    },
    voiceEnabled(newValue, oldValue) {
      if (this.disableVoice) {
        this.errorStatus = true;
        this.showValue = false;
        this.errorMessage = "Your browser does not support voice note";
      } else {
        if (newValue) {
          this.voiceNote = "Disable Voice Note";
        } else {
          this.voiceNote = "Enable Voice Note";
        }
      }
    }
  },
  computed: {
    show() {
      return this.showValue;
    },
    error() {
      return this.errorStatus;
    }
  },
  created() {
    if (window.speechSynthesis) {
      let preferredVoice = "";
      let voices = window.speechSynthesis.getVoices();
      if (preferredVoice === "") {
        preferredVoice = voices[1];
      }
      this.utter = new SpeechSynthesisUtterance();
      this.utter.rate = 1;
      this.utter.pitch = 0.5;
      this.utter.voice = preferredVoice;
    } else {
      this.disableVoice = true;
    }
  },
  methods: {
    voiceOut(text) {
      if (!this.voiceEnabled) {
        return;
      }
      if (this.disableVoice) {
        return;
      }
      this.utter.text = text;
      this.utter.onerror = () => {
        this.errorStatus = true;
        this.showValue = false;
        this.errorMessage = "Unable to produce voice note";
      };
      window.speechSynthesis.speak(this.utter);
    },
    translate() {
      if (!this.valueEntered) {
        return;
      }
      if (this.excessValue) {
        this.errorStatus = true;
        this.showValue = false;
        this.voiceOut("You are only allowed a maximum of 21 digits");
        return (this.errorMessage =
          "You are only allowed a maximum of 21 digits");
      }
      let converter = new FigureConverter();
      let answer = converter.convert(this.valueEntered);
      answer = answer.replace(
        answer.substr(0, 1),
        answer.substr(0, 1).toUpperCase()
      );

      if (answer.substr(0, 5) === "Error") {
        this.errorStatus = true;
        this.showValue = false;
        this.voiceOut(answer);
        return (this.errorMessage = answer);
      } else {
        this.message = answer;
        this.showValue = true;
        this.voiceOut(answer);
      }
    },
    alertClose() {
      this.showValue = false;
      this.errorStatus = false;
    }
  }
};
</script>
